*,
*::after,
*::before {
  box-sizing: border-box;
  outline: none;
}

blockquote,
body,
dd,
dl,
figcaption,
figure,
h1,
h2,
h3,
h4,
li,
ol,
p,
ul {
  margin: 0;
}

body {
  font-family: "Rubik";
  font-weight: 400;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  overflow-x: hidden;
  height: 100%;
}

body.hidden-y {
  overflow-y: hidden;
  padding-right: 15px;
}

ol,
ul {
  padding: 0;
  list-style: none;
}

a {
  font-family: inherit;
  text-decoration: none;
  color: inherit;
}

img {
  max-width: 100%;
  height: auto;
}

button {
  cursor: pointer;
  border: none;
}

button,
input,
select,
textarea {
  font-family: inherit;
  outline: none;
}

input:focus,
.subscription-form input:focus,
.modal-lk__textarea:focus {
  color: black;
  border: 1px solid #38B2AC;
}

.search__btn:hover {
  background: #185B5B;
}

