.selectcity {
  width: 170px;
  color: #686868;
  font-size: 15px;
  cursor: pointer;
}

.selectcity span {
  display: inline-block;
  position: relative;
}

.selectcity span:after {
  content: "";
  width: 8px;
  height: 5px;
  position: absolute;
  top: 59%;
  right: -15px;
  transform: translateY(-50%);
  background: url("../img/arrow-down.svg") no-repeat center;
}

.select_city,
.select_sorting {
  width: 170px;
}

.select_city .selectric,
.select_sorting .selectric {
  border: none;
  background: none;
}

.select_city .label,
.select_sorting .label {
  color: #686868;
  font-size: 15px;
  padding-left: 10px;
  margin-left: 0;
  font-weight: 400;
  margin-right: 20px;
}

.select_city .selectric-items li,
.select_sorting .selectric-items li {
  padding: 4px;
  padding-left: 14px;
  font-size: 14px;
}

.select_city .button,
.select_sorting .button {
  width: 8px;
  height: 5px;
  right: 18px;
  top: 54%;
  transform: translateY(-50%);
  background: url("../img/arrow-down.svg") no-repeat center;
}

.select_city .button:after,
.select_sorting .button:after {
  display: none;
}

.select-refprod,
.product-select {
  width: 100%;
}

.select-refprod .selectric,
.product-select .selectric {
  border: 1px solid #D8D8D8;
  background: white;
}

.select-refprod .label,
.product-select .label {
  font-size: 13px;
  height: 48px;
  line-height: 48px;
  padding-left: 12px;
}

.select-refprod .selectric-items li,
.product-select .selectric-items li {
  padding-left: 22px;
}

.select-refprod .button,
.product-select .button {
  width: 12px;
  height: 7px;
  right: 18px;
  top: 54%;
  transform: translateY(-50%);
  background: url("../img/down-bl.svg") no-repeat center;
}

.select-refprod .button:after,
.product-select .button:after {
