.feedbackmodal {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  max-width: 405px;
  width: 100%;
  box-shadow: 0px 4px 23px 3px rgba(0, 0, 0, 0.15);
  z-index: 500;
  background: white;
  display: none;
}

.feedback-form {
  padding: 68px 44px 57px;
  z-index: 500;
}

.feedback-form input {
  margin-bottom: 18px;
  position: relative;
  z-index: 10;
}

@media (min-width: 768px) {
  .feedback-form {
    position: relative;
  }
  .feedback-form:after {
    content: "";
    position: absolute;
    top: 0;
    left: -10px;
    width: 355px;
    height: 135px;
    background: url("../img/recall-bg-top.png") no-repeat center;
  }
  .feedback-form:before {
    content: "";
    position: absolute;
    bottom: 0;
    right: 0;
    width: 265px;
    height: 275px;
    background: url("../img/recall-bg-b.png") no-repeat center;
  }
}

.feedback-form__btn {
  width: 170px;
}

.feedbackmodal__title {
  font-weight: 500;
  font-size: 26px;
  margin-bottom: 25px;
  position: relative;
  z-index: 10;
}

@media (max-width: 767px) {
  .feedbackmodal__title {
    font-size: 20px;
  }
}

@media (min-width: 768px) {
  .feedbackmodal__title {
    padding-right: 20px;
  }
}

